import React, { useState, useEffect } from 'react'
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CLabel,
  CSelect,CButton, CFormGroup,

} from "@coreui/react";
import CIcon from '@coreui/icons-react';
import MaterialTable from "material-table";
import imageNoAction from "../../images/vektors/at_home.png";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useDispatch, useSelector } from 'react-redux';
import {GetMonevByYearAction, setFormFilterMonev } from "src/common/redux/action";
const ReportMonev = () => {

  // initialization reducer
  const dispatch = useDispatch();
  const { SetListMonevsReducer, formFilterMonev } = useSelector(
    (state) => state.reportMonevReducer
  );
  const {
    Year
  } = formFilterMonev;
  const [stateFilterShowHide, setStateFilterShowHide] = useState(false);
  const [listOfYear, setListOfyear] = useState([]);
  const backYear = () => {
    let yearNow = new Date().getFullYear();
    var decrementYear = yearNow - 5;
    let tmpArr = [];
    for (yearNow; yearNow > decrementYear; yearNow--) {
      tmpArr.push({
        key: yearNow,
        value: yearNow,
      });
    }
    setListOfyear(tmpArr);
  };

  const handleOnchangeYear = (event) => {
    let yearValue = event.target.value;
    dispatch(setFormFilterMonev("Year", yearValue));
  };

  const filterMonevByTahun = () => {
    dispatch(GetMonevByYearAction("GET_LIST_MONEVS", Year));
    setStateFilterShowHide(true);
  }

  useEffect(() => {
    backYear()
  },[])

  const columns = [
    {
      title: "Aksi",
      field: "internal_action",
      cellStyle: {
        backgroundColor: "#039be5",
        color: "#FFF",
      },
      headerStyle: {
        backgroundColor: "#039be5",
      },
      render: (rowData) => {
        return (
          <div className="row">
            <button type="button" className="btn btn-sm btn-info mr-2 ml-4">
              <CIcon name="cil-pencil" /> Detail Monev
            </button>
            ||
            <button type="button" className="btn btn-sm btn-dark mr-2 ml-4">
              <CIcon name="cil-pencil" /> Download Detail Monev
            </button>
            ||
            <button type="button" className="btn btn-sm btn-danger mr-2 ml-4">
              <CIcon name="cil-pencil" /> Hapus Monev
            </button>
          </div>
        );
      },
    },
    { title: "Provinsi", field: "provinsi" },
    { title: "Kabupaten", field: "kabupaten" },
    { title: "Kecamatan", field: "kecamatan" },
    { title: "Nama Pegawai ", field: "pegawai" },
    { title: "Jabatan", field: "jabatan" },
    { title: "Jenis Lokpri", field: "jenisLokpri" },
    { title: "Rata - rata CIQ", field: "rataRataCiq" },
    { title: "Rata - rata PAP", field: "rataRataPap" },
    { title: "Tanggal Input", field: "tanggalInput" },
  ];

  return (
    <CRow>
      <CCol md="12" xl="12" xs="12">
        <CCard>
          <CCardHeader>
            <h4 color="info" md="3">
              Daftar Laporan Monev
            </h4>
            <CRow>
              <CCol md="7">
                <CFormGroup className="form-row  mb-3">
                  <CLabel className="col-md-2">Tahun</CLabel>
                  <div className="col-md-4">
                    <CSelect onChange={handleOnchangeYear}>
                      {listOfYear.map((v) => (
                        <option key={v.key} value={v.key}>
                          {v.value}
                        </option>
                      ))}
                    </CSelect>
                  </div>
                  <div className="col-md-4">
                    <CButton color="info" onClick={filterMonevByTahun}>
                      {" "}
                      <FontAwesomeIcon className="mr-1" icon="search" />
                      Filter Monev{" "}
                    </CButton>
                  </div>
                </CFormGroup>
              </CCol>
              <CCol md="5"></CCol>
            </CRow>
          </CCardHeader>
          <CCardBody>
            {stateFilterShowHide === false ? (
              <img
                src={imageNoAction}
                className="mb-2 img"
                width="100%"
                height="100%"
              />
            ) : (
              <div>
                <MaterialTable
                  title=""
                  options={{
                    headerStyle: {
                      backgroundColor: "#01579b",
                      color: "#FFF",
                    },
                    filtering: true,
                  }}
                  columns={columns}
                  data={
                    SetListMonevsReducer.length > 0 ? SetListMonevsReducer : []
                  }
                />
              </div>
            )}
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  );
}

export default ReportMonev
